
<template>
  <viewer :images="images">
    <div
      class="imgDiv"
      v-for="(item, index) in images"
      :key="'img' + index"
      @mouseover="mouseOver(index)"
      @mouseleave="mouseLeave(index)"
    >
      <img
        :src="item.src"
        :key="item.index"
        width="100"
        height="100"
        style="margin-right: 10px"
      />
      <p class="iconStyle" v-if="item.show" @click="deleteImg(item)">
        <i class="el-icon-close"></i>
      </p>
    </div>
  </viewer>
</template>
 
 
<script>
import Vue from "vue";
import Viewer from "v-viewer";
import "viewerjs/dist/viewer.css";
Vue.use(Viewer);
Viewer.setDefaults({
  inline: false, // inline 
  button: true, //
  navbar: true, //
  title: true, //
  toolbar: true, //
  tooltip: true, //
  movable: true, //
  zoomable: true, //
  rotatable: true, //
  scalable: true, //
  transition: true, // CSS3 
  fullscreen: true, //
  keyboard: true, //
  url: "data-source",
  ready: function (e) {
    console.log(e.type, "");
  },
  show: function (e) {
    console.log(e.type, "");
  },
  shown: function (e) {
    console.log(e.type, "");
  },
  hide: function (e) {
    console.log(e.type, "");
  },
  hidden: function (e) {
    console.log(e.type, "");
  },
  view: function (e) {
    console.log(e.type, "");
  },
  viewed: function (e) {
    console.log(e.type, "");
  },
  zoom: function (e) {
    console.log(e.type, "");
  },
  zoomed: function (e) {
    console.log(e.type, "");
  },
});

export default {
  props: {
    imageList: Array | Object, //string array
  },
  watch: {
    //
    imageList(val) {
      let type = Array.isArray(val);
      if (!type) {
        this.changeArr(val.url);
      } else {
        this.changeArrT(val);
      }
    },
  },
  data() {
    return {
      isShow: false, //
      images: [
        {src:'https://ss0.bdstatic.com/70cFvHSh_Q1YnxGkpoWK1HF6hhy/it/u=3272199364,3404297250&fm=26&gp=0.jpg',index:1},
        {src:'https://ss3.bdstatic.com/70cFv8Sh_Q1YnxGkpoWK1HF6hhy/it/u=3267295166,2381808815&fm=26&gp=0.jpg',index:2},
        {src:'https://ss0.bdstatic.com/70cFvHSh_Q1YnxGkpoWK1HF6hhy/it/u=3429654663,2972188411&fm=26&gp=0.jpg',index:3},
        {src:'https://ss3.bdstatic.com/70cFv8Sh_Q1YnxGkpoWK1HF6hhy/it/u=3597323878,2962972725&fm=26&gp=0.jpg',index:4},
      ],
    };
  },

  components: {},

  computed: {},

  created() {},

  mounted() {
    let type = Array.isArray(this.imageList);
    if (!type) {
      this.changeArr(this.imageList.url);
    } else {
      this.changeArrT(this.imageList);
    }
  },

  methods: {
    /** */
    changeArr(str) {
      if (str.url == undefined) {
        return;
      }
      let obj = {
        src: str.url,
        index: 1,
        show: false,
        other: str,
      };
      let arr = [];
      arr.push(obj);
      this.images = arr;
    },
    /** */
    changeArrT(arr) {
      if (arr.length == 0) {
        this.images = [];
        return;
      }
      let new_arr = [];
      arr.forEach((item, index) => {
        let obj = {
          other: item,
          src: item.url,
          index: index,
          show: false,
        };
        new_arr.push(obj);
      });

      this.images = new_arr;
    },
    //
    mouseOver(index) {
      this.images[index].show = true;
    },
    //
    mouseLeave(index) {
      this.images[index].show = false;
    },
    /**
     * 
     * */
    deleteImg(item) {
      this.$emit("deleteImg", item);
    },
  },
};
</script>
<style  scoped>
.imgDiv {
  display: inline-block;
  position: relative;
}
.iconStyle {
  width: 30px;
  height: 40px;
  position: absolute;
  top: 0;
  right: 10px;
  font-size: 25px;
  text-align: center;
  background: rgba(0, 0, 0, 0.7);
  color: #fff;
}
</style>