
<template>
  <div class="uploadImgBody" v-if="Refresh">
    <!---->
    <imgView
      v-if="!showFileList"
      class="imgViewDiv"
      :imageList="strlist || []"
      @deleteImg="deleteImg"
    ></imgView>
    <!---->
    <el-upload
      v-if="isEdit"
      class="upload-image"
      ref="upload"
      :action="action"
      :headers="headers"
      :multiple="multiple"
      :data="data"
      :name="name"
      :with-credentials="cookieOK"
      :show-file-list="showFileList"
      :drag="drag"
      :accept="accept"
      :list-type="listType"
      :auto-upload="autoUpload"
      :file-list="fileList"
      :disabled="is_disabled"
      :on-preview="handlePreview"
      :on-remove="handleRemove"
      :on-success="handleSuccess"
      :on-error="handleError"
      :on-progress="handleProgress"
      :on-exceed="handleExceed"
      :on-change="onChange"
      :before-upload="beforeUpload"
      :before-remove="beforeRemove"
      :http-request="httpRequest"
      :limit="limit"
    >
      <div></div>
      <div slot="default" class="uploadImgDiv">
        <i class="el-icon-plus"></i>
        <!---->
        <div slot="tip" class="el-upload__tip" v-if="tip_text != ''">
          {{ tip_text }}
        </div>
      </div>

      <!---->
      <div slot="file" slot-scope="{ file }">
        <img class="el-upload-list__item-thumbnail" :src="file.url" alt="" />
        <span class="el-upload-list__item-actions">
          <span
            class="el-upload-list__item-preview"
            @click="handlePictureCardPreview(file)"
          >
            <i class="el-icon-zoom-in"></i>
          </span>
          <span
            v-if="!disabled"
            class="el-upload-list__item-delete"
            @click="handleRemove(file)"
          >
            <i class="el-icon-delete"></i>
          </span>
        </span>
      </div>
    </el-upload>
    <el-dialog :visible.sync="dialogVisible">
      <img width="100%" :src="dialogImageUrl" alt="" />
    </el-dialog>
  </div>
</template>
 
<script>
import imgView from "./imgView";
//element
export default {
  props: {
    /**
     * 
     * */
    autoUpload: {
      // 
      type: Boolean,
      default: false,
    },
    action: {
      //
      type: String,
      default: "",
    },
    headers: {
      //
      type: Object,
      default: () => {
        return {};
      },
    },
    data: {
      //
      type: Object,
      default: () => {
        return {};
      },
    },
    name: {
      //
      type: String,
      default: "file",
    },
    cookieOK: {
      // cookie 
      type: Boolean,
      default: true,
    },
    /**
     * 
     * */
    showFileList: {
      //
      type: Boolean,
      default: false,
    },
    drag: {
      //
      type: Boolean,
      default: true,
    },
    accept: {
      //--
      type: String,
      // default:'.doc,.docx,application/msword,application/vnd.openxmlformats-officedocument.wordprocessingml.document'
      default: ".jpg,.jpeg,.png",
    },
    listType: {
      //  - text/picture/picture-card
      type: String,
      default: "picture-card",
    },
    fileList: {
      //,
      type: Array,
    },
    is_disabled: {
      //truefalse
      type: Boolean,
      default: false,
    },
    multiple: {
      //
      type: Boolean,
      default: true,
    },
    limit: {
      //
      type: Number,
    },

    tip_text: {
      //
      type: String,
      default: "",
    },
    /**
     * 
     * */
    needFromUpload: {
      // form,file formUpload  
      type: Boolean,
      default: false,
    },
    isEdit: {
      //
      type: Boolean,
      default: true,
    },
    scid: {
      type: Array,
    },
    perid: {
      type: Array,
      defaul: null,
    },
  },
  watch: {},
  data() {
    return {
      Refresh: true, //
      dialogImageUrl: "",
      dialogVisible: false,
      disabled: false,
      strlist: "",
      id: "",
      idindex: "",
    };
  },
  mounted() {
    this.strlist = this.fileList;
  },
  methods: {
    /**
     * -
     * */
    //
    handleRemove(file, fileList) {
      console.log(":");
      console.log(file);
    },
    //
    handlePreview(file) {
      console.log("" + file);
    },
    //
    handleSuccess(response, file, fileList) {
      console.log("");
    },
    //
    handleError(err, file, fileList) {
      console.log("");
    },
    //
    handleProgress(event, file, fileList) {
      console.log(file);
    },
    //
    handleExceed(files, fileList) {
      console.log(this.scid);
      this.$message("");
    },
    //
    httpRequest() {},
    // false  Promise  reject
    beforeRemove(file, fileList) {
      console.log("" + file);
      this.strlist.forEach((item, index) => {
        if (item == file) {
          this.strlist.splice(index, 1);
        }
      });
    },

    /**
     * 
     */
    onChange(file, fileList) {
      this.strlist = fileList;
      /*    console.log("");
      console.log(fileList);  */
      this.$emit("onChange", fileList[0].raw);
    },
    /**
     *  false  Promise  reject
     */
    beforeUpload(file) {
      console.log(file);
    },
    /**
     * -
     * */
    submitUpload() {
      this.$refs.upload.submit();
    },
    // before-upload 
    clearFiles() {
      this.$refs.upload.clearFiles();
      this.strlist = [];
    },
    //
    abortFiles(file) {
      this.$refs.upload.abort(file);
    },

    /**
     * 
     * */
    submitUploadSD() {
      let arr = this.strlist;
      let str = {
        fileList: arr,
      };
      this.$emit("uploadFile", str);
    },

    /**
     * 
     * */
    RefreshUpload() {
      let arr = this.strlist;
      this.Refresh = false;
      this.$nextTick(() => {
        this.Refresh = true;
      });
    },

    /**
     * 
     * */
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    //
    deleteImg(item) {
      let uid = item.other.uid;
      this.strlist.forEach((item, index) => {
        if (item.uid == uid) {
          this.idindex = index;
          this.strlist.splice(index, 1);
        }
      });
      if (this.scid.length > 0) {
        //
        this.scid.forEach(async (scstr, index) => {
          if (this.idindex == index) {
            this.id = scstr;
            console.log(this.id);
            let str = {
              id: this.id,
            };
            let { data } = await this.$http.merdelete(str);
            console.log(data);
            if (data.code == "0000") {
              this.$message.success("");
            } else {
              return;
            }
          }
        });
      }
      if ( this.perid != null) {
        //
        this.perid.forEach(async (scstr, index) => {
          if (this.idindex == index) {
            this.id = scstr;
            console.log(this.id);
            let str = {
              id: this.id,
            };
            let { data } = await this.$http.sealdel(str);
            console.log(data);
            if (data.code == "0000") {
              this.$message.success("");
            } else {
              return;
            }
          }
        });
      }
    },
  },
  components: {
    imgView,
  },
  beforeDestroy() {},
};
</script>
 
<style  scoped>
.uploadImgBody {
  height: 100px;
  border: 0 !important;
}
.imgViewDiv {
  height: 100px;
  display: inline-block;
  vertical-align: top;
}
.upload-image {
  width: 100px;
  height: 100px;
  display: inline-block;
  border: 0;
}

.upload-image > :nth-child(1) {
  width: 100px;
  height: 100px;
  line-height: 105px;
  border: 0;
}
.upload-image > :nth-child(1) > :nth-child(1) {
  width: 100px;
  height: 100px;
  border: 1px solid #c0cdd0;
}
</style>